.PageContainer {
    width: 90%;
    margin: 20px auto;
}

.Header {
    margin-left: 12px;
    font-weight: 600;
    font-size: 40px;
}

.Cards {
    margin: 24px 0;
    display: flex;
    flex-direction: row;
}

.Card {
    margin: 0 12px !important;
}

@media (max-width: 499px) {
    .PageContainer {
        width: 100%;
        margin: 20px 0 20px 20px;
    }

    .Header {
        margin-left: 0;
        font-weight: 600;
        font-size: 24px;
    }

    .Cards {
        margin: 24px 0;
        display: flex;
        overflow: auto;
        padding-bottom: 16px;
        height: 100%;
    }

    .Card {
        margin: 0 4px !important;
        font-size: 8px;
        display: flex;
        min-width: 50%;
    }
}
